@import url(https://fonts.jimstatic.com/css?family=Poppins:400,500,600,700,700italic&subset=latin,latin-ext,cyrillic-ext,cyrillic);
* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
 
  background-size: auto;
}

body {
  background-image: url("https://wallpaperbat.com/img/343970-matrix-wallpaper-for-desktop.jpg");
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  
  text-align: center;
}

h1 {
  
}

h2 {
  color: #FFFFFF;
text-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #49ff18, 0 0 30px #49FF18, 0 0 40px #49FF18, 0 0 55px #49FF18, 0 0 75px #49ff18;
margin: 30px;
}

img {
  max-width: 50px;
  margin: 75px;
}

img:hover {
  transition: 0.9s;
  transform: scale(1.10);
}

.countdown {
    margin-top: 200px;
    font-size: x-large;
    color: rgb(119, 250, 102);
    text-shadow: 5px 5px 5px  rgb(0, 0, 0);
    
}
